}\r\n    &:focus + .checkbox__label:before {\r\n    }\r\n    &:checked + .checkbox__label:before {\r\n      background-image: url(./assets/images/icons/check.svg);\r\n      background-position: center;\r\n      background-size: 1.4rem;\r\n      background-repeat: no-repeat;\r\n      @media (max-width: 48em) {\r\n        background-size: 3rem;\r\n      }\r\n    }\r\n  }\r\n  // .checkbox__label\r\n  &__label {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    position: relative;\r\n    column-gap: 2.4rem;\r\n    cursor: pointer;\r\n    &:before {\r\n      content: '';\r\n      flex: 0 0 2.4rem;\r\n      width: 2.4rem;\r\n      height: 2.4rem;\r\n      border: 1px solid $white;\r\n      transition: border 1px solid;\r\n    }\r\n    @media (max-width: 48em) {\r\n      column-gap: 2.5rem;\r\n      &::before {\r\n        flex: 0 0 4.8rem;\r\n        width: 4.8rem;\r\n        height: 4.8rem;\r\n      }\r\n    }\r\n  }\r\n  // .checkbox__text\r\n  &__text {\r\n    transition: color 0.3s ease;\r\n    &.txt {\r\n      line-height: 155%;\r\n      @media (max-width: 48em) {\r\n        font-size: 2.7rem;\r\n      }\r\n    }\r\n  }\r\n}\r\n",".ymaps3x0--main-engine-container {\r\n  filter: grayscale(1);\r\n  -ms-filter: grayscale(1);\r\n  -webkit-filter: grayscale(1);\r\n  -moz-filter: grayscale(1);\r\n  -o-filter: grayscale(1);\r\n}\r\n\r\n.marker {\r\n  position: relative;\r\n  box-sizing: border-box;\r\n  width: 8.2rem;\r\n  transform: translate(-50%, calc(-50% - 24px));\r\n  cursor: pointer;\r\n  @media (max-width: 48em) {\r\n    width: 10rem;\r\n  }\r\n}\r\n","@import 'base/forms/forms';\r\n\r\n// popup styles\r\n// @import \"base/popup\";\r\n\r\n// spoiler styles\r\n// @import \"base/spoilers\";\r\n\r\n// tab styles\r\n// @import \"base/tabs\";\r\n\r\n// map styles\r\n@import 'base/maps';\r\n\r\n// showmore styles\r\n// @import \"base/showmore\";\r\n\r\n// fullscreen styles\r\n// @import \"base/fullscreen\";\r\n\r\n// sticky block styles\r\n// @import \"base/sticky\";\r\n\r\n// ibg styles\r\n[class*='-ibg'] {\r\n  position: relative;\r\n  img {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 0;\r\n    object-fit: cover;\r\n  }\r\n}\r\n[class*='-ibg_contain'] {\r\n  img {\r\n    object-fit: contain;\r\n  }\r\n}\r\n\r\n// templates ===================================================================\r\n\r\n// @extend %pattern name;\r\n// snippet ( SCSS ): ex\r\n\r\n// list counter\r\n%listCounter {\r\n  list-style-type: none;\r\n  counter-reset: item;\r\n  li {\r\n    position: relative;\r\n    &:before {\r\n      counter-increment: item;\r\n      content: counter(item);\r\n      position: absolute;\r\n      left: 0;\r\n      top: 0;\r\n    }\r\n  }\r\n}\r\n\r\n// responsive video\r\n%responsiveVideo {\r\n  position: relative;\r\n  overflow: hidden;\r\n  height: 0;\r\n  padding-bottom: 56.25%;\r\n  video,\r\n  iframe,\r\n  object,\r\n  embed {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n}\r\n\r\n// video as background\r\n%videoBackground {\r\n  video,\r\n  iframe,\r\n  object,\r\n  embed {\r\n    position: fixed;\r\n    top: 50%;\r\n    left: 50%;\r\n    min-width: 100%;\r\n    min-height: 100%;\r\n    width: auto;\r\n    height: auto;\r\n    z-index: -100;\r\n    transform: translateX(-50%) translateY(-50%);\r\n    background-size: cover;\r\n  }\r\n}\r\n\r\n// gray filter\r\n%grayfilter {\r\n  transition: all 0.3s ease 0s;\r\n  filter: grayscale(1);\r\n  @media (any-hover: hover) {\r\n    &:hover {\r\n      filter: grayscale(0);\r\n    }\r\n  }\r\n}\r\n\r\n// no select\r\n%noselect {\r\n  user-select: none;\r\n}\r\n\r\n// mirror reflection\r\n%mirror {\r\n  transform: scale(-1, 1);\r\n}\r\n\r\n// smooth scroll\r\n%smoothscroll {\r\n  -webkit-overflow-scrolling: touch;\r\n}\r\n\r\n// hide scroll\r\n%hidescroll {\r\n  &::-webkit-scrollbar {\r\n    display: none;\r\n  }\r\n}\r\n\r\n%line {\r\n  content: '';\r\n  position: absolute;\r\n  width: 2px;\r\n  height: 100%;\r\n  background-color: #505050;\r\n  transform-origin: left top;\r\n}\r\n\r\n%swiperFix {\r\n  &.swiper-slide {\r\n    opacity: 0 !important;\r\n    visibility: hidden !important;\r\n  }\r\n  &.swiper-slide-active {\r\n    opacity: 1 !important;\r\n    visibility: visible !important;\r\n  }\r\n}\r\n","body {\n  position: relative;\n  &::after {\n    content: '';\n    position: fixed;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    background: rgba(255, 255, 255, 0.05);\n    backdrop-filter: blur(4rem);\n    opacity: 0;\n    visibility: hidden;\n    transition: all 0.3s ease;\n  }\n  @media (max-width: 48em) {\n    .menu-open & {\n      &::after {\n        opacity: 1;\n        visibility: visible;\n      }\n    }\n  }\n}\n\n// -------------------------------- elements --------------------------------\n\n// sidebar\n.sidebar {\n  position: absolute;\n  z-index: 100;\n  top: 0;\n  left: auto;\n  padding: 3.6rem 2.3rem 9rem 2.3rem;\n  height: 100%;\n  background-color: rgba(106, 106, 106, 0.2);\n  backdrop-filter: blur(1.9rem);\n  @media (max-width: 48em) {\n    padding: 4rem 0;\n    left: 0;\n    width: 100%;\n    height: 16.4rem;\n    background-color: rgba(255, 255, 255, 0.05);\n    backdrop-filter: blur(4.6rem);\n    transition: all 0.3s ease;\n    .menu-open & {\n      background-color: transparent;\n      backdrop-filter: blur(0);\n    }\n  }\n  @media (min-width: 48em) {\n    position: fixed;\n    width: 15rem;\n    transition: width 0.4s ease;\n    .menu-open & {\n      width: 30.7rem;\n    }\n  }\n\n  // .sidebar__inner\n\n  &__inner {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    height: 100%;\n    @media (min-width: 48em) {\n      flex-direction: column;\n    }\n    @media (max-width: 48em) {\n      padding: 0 3.2rem;\n    }\n  }\n\n  // .sidebar__logo-wrap\n\n  &__logo-wrap {\n    width: 10.4rem;\n    @media (max-width: 48em) {\n      position: relative;\n      z-index: 105;\n      width: 13.6rem;\n    }\n    @media (min-width: 48em) {\n      margin-bottom: 8.6rem;\n    }\n  }\n\n  // .sidebar__logo\n\n  &__logo {\n    object-fit: contain;\n  }\n\n  // .sidebar__menu\n\n  &__menu {\n  }\n  // .sidebar__socials\n\n  &__socials {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    row-gap: 1.9rem;\n    @media (max-width: 48em) {\n      flex-direction: row;\n      column-gap: 5rem;\n    }\n  }\n\n  // .sidebar__socials-item\n\n  &__socials-item {\n    @media (min-width: 48em) {\n      &._mobile-only {\n        display: none;\n      }\n    }\n  }\n\n  // .sidebar__socials-link\n\n  &__socials-link {\n    display: inline-block;\n    width: 3.2rem;\n    @media (max-width: 48em) {\n      width: 6rem;\n    }\n  }\n\n  // .sidebar__socials-icon\n\n  &__socials-icon {\n    object-fit: contain;\n  }\n\n  // .sidebar__tel\n\n  &__tel {\n    font-family: Assistant;\n    opacity: 0;\n    visibility: hidden;\n    white-space: nowrap;\n    transition: all 0.4s ease;\n    .menu-open & {\n      opacity: 1;\n      visibility: visible;\n    }\n    @media (max-width: 48em) {\n      margin-bottom: 5rem;\n      &.txt {\n        font-size: 6rem;\n        font-weight: 700;\n      }\n    }\n  }\n}\n\n// hamburger\n.hamburger {\n  display: inline-flex;\n  flex-direction: column;\n  align-items: center;\n  row-gap: 0.6rem;\n  width: 3.4rem;\n  &::before,\n  &::after {\n    content: '';\n  }\n  &::before,\n  &::after,\n  span {\n    width: 100%;\n    height: 0.2rem;\n    background-color: $white;\n  }\n  span {\n    transform: scaleX(0.7);\n    transition: transform 0.4s ease;\n  }\n  @media (max-width: 48em) {\n    row-gap: 1.2rem;\n    width: 6.5rem;\n    &::before,\n    &::after,\n    span {\n      height: 0.4rem;\n      transition: all 0.3s ease;\n    }\n    .menu-open & {\n      position: absolute;\n      &::before,\n      &::after,\n      span {\n        opacity: 0;\n        visibility: hidden;\n      }\n    }\n  }\n  @media (min-width: 48em) {\n    transition: all 0.3s ease;\n    .menu-open & {\n      opacity: 0;\n      visibility: hidden;\n    }\n  }\n}\n\n// menu\n.menu {\n  position: relative;\n  @media (min-width: 48em) {\n    display: flex;\n    align-items: center;\n    flex: 1 1 auto;\n  }\n\n  // .menu__hamburger-wrap\n\n  &__hamburger-wrap {\n    @media (any-hover: hover) {\n      &:hover {\n        .hamburger {\n          span {\n            transform: scaleX(0.99);\n          }\n        }\n      }\n    }\n    @media (max-width: 48em) {\n      position: relative;\n      z-index: 105;\n      display: flex;\n      align-items: center;\n      &::after {\n        content: '';\n        position: absolute;\n        width: 6.5rem;\n        height: 6.5rem;\n        background-image: url(./assets/images/icons/cross.svg);\n        background-size: contain;\n        background-position: center;\n        background-repeat: no-repeat;\n        opacity: 0;\n        visibility: hidden;\n        transition: all 0.3s ease;\n      }\n      .menu-open & {\n        &::after {\n          position: static;\n          opacity: 1;\n          visibility: visible;\n        }\n      }\n    }\n    @media (min-width: 48em) {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      row-gap: 1.6rem;\n      transform: translate(-50%, -50%);\n      .menu-open & {\n        opacity: 0;\n        visibility: hidden;\n      }\n    }\n  }\n\n  // .menu__hamburger\n\n  &__hamburger {\n  }\n\n  // .menu__hamburger-txt\n\n  &__hamburger-txt {\n    text-transform: uppercase;\n    color: $white;\n  }\n\n  // .menu__body\n\n  &__body {\n    opacity: 0;\n    visibility: hidden;\n    // transition: all 0.4s ease;\n    @media (max-width: 48em) {\n      position: fixed;\n      top: 0;\n      left: 0;\n      right: 0;\n      padding-top: 40rem;\n      padding-bottom: 12rem;\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      height: 100vh;\n      overflow: auto;\n    }\n    .menu-open & {\n      opacity: 1;\n      visibility: visible;\n    }\n  }\n\n  // .menu__list\n\n  &__list {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    row-gap: 4.8rem;\n    text-align: center;\n    @media (max-width: 48em) {\n      margin-bottom: auto;\n      row-gap: 6.4rem;\n    }\n  }\n\n  // .menu__item\n\n  &__item {\n  }\n\n  // .menu__link\n\n  &__link {\n    position: relative;\n    text-transform: uppercase;\n    &::after {\n      content: '';\n      position: absolute;\n      bottom: -0.3rem;\n      left: 0;\n      width: 100%;\n      height: 1px;\n      background-color: $white;\n      opacity: 0;\n      transform: scaleX(0);\n      transform-origin: center;\n      transition: all 0.5s ease;\n    }\n    @media (any-hover: hover) {\n      &:hover {\n        &::after {\n          opacity: 1;\n          transform: scaleX(1);\n        }\n      }\n    }\n    @media (max-width: 48em) {\n      font-size: 4rem;\n    }\n  }\n}\n\n// slider nav btn\n.nav-btn {\n  flex: 0 0 6rem;\n  width: 6rem;\n  height: 6rem;\n  border-radius: 50%;\n  svg {\n    width: 6rem;\n    height: 6rem;\n    transition: fill 0.4s ease;\n    path {\n      transition: stroke 0.4s ease;\n    }\n  }\n  @media (any-hover: hover) {\n    &:hover {\n      svg {\n        fill: $white;\n        path {\n          stroke: $black;\n        }\n      }\n    }\n  }\n  @media (max-width: 48em) {\n    flex: 0 0 8rem;\n    width: 8rem;\n    height: 8rem;\n    svg {\n      width: 8rem;\n      height: 8rem;\n    }\n  }\n}\n\n// slider fraction\n.sl-fraction {\n  display: flex;\n  align-items: center;\n  column-gap: 5rem;\n\n  // .sl-fraction__progress\n\n  &__progress {\n    display: none;\n    @media (min-width: 48em) {\n      position: relative;\n      display: block;\n      flex: 0 0 36.8rem;\n      width: 36.8rem;\n      height: 2px;\n      backgro